import * as echarts from '@/components/mpvue-echarts/echarts.min.js';
import DayOfStatistics from '@/components/staff/day-of-statistics.vue';
import BillStatistics from '@/components/staff/bill-statistics.vue';
import { todayDataReq } from '@/api/stats.js';
import { mapActions, mapGetters } from 'vuex';
var echartsInstans = null;
export default {
	components: {
		DayOfStatistics,
		BillStatistics
	},
	data() {
		return {
			echarts,
			statsData: {},
			dataFlag: 'I' //进出口标识
		};
	},
	computed: {
		...mapGetters({
			todayData: 'stats/todayData'
		})
	},
	watch: {
		dataFlag(n) {
			if (n === 'I') {
				this.statsData = this.todayData['importData'];
			}
			if (n === 'E') {
				this.statsData = this.todayData['exportData'];
			}
			//动态更新charts数据




		}
	},





	methods: {
		...mapActions({
			setTodayData: 'stats/setTodayData'
		}),
		changeFlag(flag) {
			this.dataFlag = flag;
		},
		async reqData() {
			//请求数据
			const todayData = await todayDataReq();
			this.setTodayData(todayData);
			this.statsData = this.todayData['importData'];
		},
		//生成 返回option
		optionsConfig() {
			let { xAxis, axisValues } = this.statsData.goodsRank;
			let bgData = [];
			var max = axisValues.reduce((a, b) => {
				return b > a ? b : a;
			});
			axisValues.forEach((item, index) => {
				bgData.push(max);
			});
			let option = {
				title: {
					show: false,
					text: ''
				},
				grid: {
					top: '5%',
					right: '15%',
					bottom: '5%',
					left: '15%',
					borderColor: '#ffffff'
				},
				xAxis: {
					axisTick: 'none',
					splitLine: 'none',
					axisLabel: false,
					axisLine: {
						lineStyle: {
							color: '#868686'
						}
					}
				},
				yAxis: [
					{
						axisTick: 'none',
						splitLine: 'none',
						axisLabel: {
							textStyle: {
								color: '#6B6B6B'
							}
						},
						axisLine: {
							lineStyle: {
								color: '#868686'
							}
						},
						data: xAxis
					},
					{
						type: 'category',
						axisLine: 'none',
						axisLabel: {
							textStyle: {
								color: '#6B6B6B'
							}
						},
						data: axisValues
					}
				],
				series: [
					{
						barWidth: 10,
						type: 'bar',
						barGap: '-60%',
						data: bgData,
						itemStyle: {
							normal: {
								color: '#FDE1E4'
							}
						}
					},
					{
						barWidth: 10,
						type: 'bar',
						yAxisIndex: 1,
						data: axisValues,
						itemStyle: {
							normal: {
								color: function(params) {
									var myColor = ['#FC8D52', '#48CFAE', '#EC87BF', '#FFCE55', '#FD7C76'];
									return myColor[params.dataIndex];
								}
							}
						}
					}
				]
			};
			return option;
		},
		async onInitGoodsSort(e) {




			await this.reqData();
			let { width, height } = e;
			let canvas = this.$refs.goodsSortBar.canvas;
			echarts.setCanvasCreator(() => canvas);
			const chart = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			let option = this.optionsConfig();
			chart.setOption(option);
			this.$refs.goodsSortBar.setChart(chart);
			echartsInstans = chart;
		}
	}
};
